
.doctordetail /deep/ .head{
	top: 0rem !important;
}
.doctordetail-head {
  padding: 10px 0;
  padding-left: 330px;
  padding-top: 20px;
  font-size: 1.5vw;
}
.doctordetail .head{
	left:200px;
}
.doctordetail .head-icon {
  width: auto;
  height: 80px;
  margin-left: 20px;
  margin-right: 20px;
  vertical-align: middle;
  border-radius: 50%;
}
.doctordetail .head-conent {
  display: inline-block;
  vertical-align: middle;
	max-width: 10rem;
}
.head-conent > p:nth-child(1) {
  margin-bottom: 5px;
}
.doctordetail .name {
  font-weight: bold;
  margin-right: 15px;
  font-size: 1.8vw;
}
.doctordetail .content-list{
  padding: 0 5%;
  padding-left: 240px;
}
.doctordetail .list-item {
  padding: 20px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 1px solid #eee;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 1.5vw;
}
.doctordetail .list-item > div {
  text-align: center;
  vertical-align: middle;
}
.doctordetail .list-item .item-left {
  width: 20%;
}
.doctordetail .list-item .item-center {
  width: 60%;
}
.doctordetail .list-item .item-right {
  width: 20%;
}
.doctordetail .item-right span {
  vertical-align: middle;
  display: inline-block;
  background: #fc9452;
  color: #fff;
  padding: 8px 20px;
  border-radius: 5px;
}
.doctordetail .item-right img {
  width: auto;
  height: 25px;
  margin: 0 15px 0 10px;
  vertical-align: middle;
}
.item-right.full span {
  background: #3c9bff;
}
.doctordetail .list-item .item-left {
  /* text-align: left; */
}
.item-left > span:nth-child(1) {
  margin-left: 30px;
}
span.few {
  background: #fc9452;
}
span.more {
  background: #3c9bff !important;
}
span.zero {
  background: #eee !important;
  color: #999 !important;
}
